<template>
  <div class="stock-prize">
    <div @click="selectStockPrize">
      <img src="../assets/stock01.png">
    </div>
    <div @click="gotoVoteStock">
      <img src="../assets/stock02.png">
    </div>

  </div>
</template>

<script>

import { selectStockPrize } from '@/api/prize'
import { Toast, Indicator } from 'mint-ui';
import jsAPI from '../libs/jsAPI'
export default {
  name: 'StockPrize',
  data() {
    return {

    }
  },
  methods: {
    gotoVoteStock() {
      let that = this;
      let url = 'http://qszg.stcn.com/appvote/company.jsp?f=NXBhdzVwZTI1THVqNksrQjVZaTQ=&from=groupmessage&isappinstalled=0';
      let title = '2019优秀证券公司APP评选！';
      let description = '请为新时代证券APP投上您宝贵的一票！';
      console.log("description:" + description);
      window.shareStockCallBack = function (data) {
        console.log('shareCallBack');
        console.log(data);
        that.selectStockPrize();
      }
      jsAPI.shareCallBack(url, title, description, 'shareStockCallBack');

    },
    selectStockPrize() {
      selectStockPrize().then(res => {
        if (res.data.resCode == 0) {
          //成功请求数据
          Toast({
            message: '分享成功',
            position: 'middle',
            duration: 2000
          });

        } else {
          Toast({
            message: res.data.resMsg,
            position: 'middle',
            duration: 2000
          });
        }
      });
    }
  }
}
</script>

<style lang="scss">
.stock-prize {
  background: #00236a;
  min-height: 100vh;
  img {
    width: 100%;
    height: auto;
    display: block;
  }
}
